@charset "utf-8";
//*********************************
3. Header
*********************************/

.header
{
	position: fixed;
	top: 110px;
	left: 0;
	width: 100%;
	z-index: 100;
	box-shadow: 0px 5px 20px rgba(0,0,0,0.05);
	-webkit-transition: all 200ms ease;
	transition: all 200ms ease;
}
.header.scrolled
{
	top: -40px;
}

/*********************************
3.1 Top Bar
*********************************/

.top_bar
{
	bottom:10px;
	width: 100%;
	background: #14bdee;
}
.top_bar_container
{
	width: 100%;
	height: 100%;
}
.top_bar_content
{
	
	width: 100%;
	height: 45px;
}
.top_bar_contact_list li
{
	display: inline-block;
}
.question
{
	font-family: 'Roboto Slab', serif;
	font-size: 14px;
	color: #FFFFFF;
	margin-left: 20px;
}
.top_bar_contact_list li i,
.top_bar_contact_list li > div
{
	display: inline;
	font-size: 14px;
	line-height: 0.75;
	position: relative;
	top: 0px;
	bottom: 0;
	color: #FFFFFF;
}
.top_bar_contact_list li > div
{
	margin-left: 1px;
}
.top_bar_contact_list li:not(:last-child)
{
	margin-right: 21px;
}

/*********************************
3.2 Header Content
*********************************/

.header_container
{
	bottom: 25px;
	width: 100%;
	background: #FFFFFF;
	height:40px;
}
.header_content
{
	height: 70px;
	-webkit-transition: all 200ms ease;
	transition: all 200ms ease;
}
.header.scrolled .header_content
{
	height: 40px;
}
/*********************************
social media iocons in the top_nav
**********************************/
.social-btns .btn,
.social-btns .btn:before,
.social-btns .btn .fa {
	-webkit-transition: all 0.35s;
	transition: all 0.35s;
